<template>
  <div class="alert-box" :style="alertStyle">
    <p class="title">{{title}}</p>
    <div class="alert-btn-wrapper clearfix">
      <p class="alert-btn alert-btn-left" @click="handleBtnEventLeft">{{btnTextLeft}}</p>
      <p class="alert-btn alert-btn-right" @click="handleBtnEventRight">{{btnTextRight}}</p>
    </div>
    <i class="close" @click="close"></i>
  </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    title: {
      type: String,
      default: ''
    },
    btnTextLeft: {
      type: String,
      default: ''
    },
    btnTextRight: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    width: {
      type: Number
    },
    height: {
      type: Number
    },
    absTop: {
      type: Number,
      default: 0
    },
    absLeft: {
      type: Number,
      default: 0
    }
  },
  methods: {
    close () {
      this.$emit('hanleClose')
    },
    handleBtnEventLeft () {
      this.$emit('handleBtnLeftEvent')
    },
    handleBtnEventRight () {
      this.$emit('handleBtnRightEvent')
    }
  },
  computed: {
    alertStyle () {
      return {
        width: this.width ? this.width + 'px' : '100%',
        height: this.height ? this.height + 'px' : '100%',
        top: this.absTop + 'px',
        left: this.absLeft + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .alert-box {
    position: absolute;
    z-index: 101;
    background: #fff;
    /*box-shadow: 0 8px 16px 0 rgba(219,219,219,0.84);*/
    box-shadow: 0 12px 28px 0 #B6BBBB;
    border-radius: 4px;
    .title {
      font-size: 16px;
      color: #2A3449;
      padding: 40px 0 32px 20px;
    }
    .alert-btn-wrapper {
      text-align: right;
      padding-right: 20px;
      float: right;
      .alert-btn {
        cursor: pointer;
        font-size: 14px;

        width: 80px;
        height: 36px;
        line-height: 36px;
        text-align: center;
        background: #FFFFFF;
        border: 1px solid #D5D8D8;
        border-radius: 18px;
      }
      .alert-btn-left{
        float: left;
        color: #A5A8A8;
        margin-right:16px;
      }
      .alert-btn-right{
        float: left;
        color: #FF4B4B;
        border: 1px solid #FF4B4B;
      }
    }

    .close {
      cursor: pointer;
      position: absolute;
      top: 16px;
      right: 16px;
      width: 20px;
      height: 20px;
      background: url("../assets/images/pop_close.png");
      background-size: 20px 20px;
    }
  }
</style>
